<script>
import DomMixin from '@/mixins/domMixin';
import { EMOJI } from '@/utils';
import { event } from 'vue-gtag'

export default {
    mixins: [
      DomMixin,
    ],
    created() {
        this.domSetDocumentBackgroundUrl(require('/public/img/bg-night-1080p.png'));
    },
    data() {
      return {
        activeTab: 'about',
        activeAnimation: true,
        commentModel: '',
        EMOJI,
